import logo from './logo.svg';
import './App.css';
import SideBar from './comp/SideBar';
import WeatherPortal from './comp/WeatherPortal';
import {
  BrowserRouter as Router,
  Route,
} from "react-router-dom";
import ApiSaver from './comp/Context/provider';
import axios from 'axios';


function App() {

  function getLocation() {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(async (position)=>{
        console.log(position);  
        let data = position.coords.latitude;
        let data2 = position.coords.longitude;         
          let result = await axios.post('/',{"lat":data,"long":data2});
          console.log(result);

      },(error)=>{
         document.body.innerHTML="Please Provide location";
         alert("You Have Denied the location , Since its a Weather App it Require Data , Please ReOpen the Domain");
      });
    }else { 
       console.error("UserREject");
    }
  }


return <>
 <Router>
  <ApiSaver>
     <WeatherPortal></WeatherPortal>
  </ApiSaver>
 </Router>
</>

}

export default App;
